/**
 * =============================
 * Header, Footer, Navigation, Mobile Navigation
 * =============================
 * 
 * Table of Contents: 
 * ------------------
 * 1.0 - General header style
 * 2.0 - Navigation style
 * 3.0 - Mobile Nav style
 * 4.0 - Footer styles
 */


/**
 * =============================
 * 1.0 - General header style
 * =============================
 */

.site-masthead {
    background-color: #fff;
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    box-shadow: 0 2px 4px rgba(#000, 0.1);
    padding: 0 20px;

    body.nav-is-stuck & { position: fixed; }
}

.header-container {
    height: 100%;
    max-width: $container-width;
    margin: 0 auto;
    position: relative;
}

.company-branding {
    display: table;
    vertical-align: middle;
    height: $header-height;

    @include breakpoint('below', 600px) {
        height: $header-height-mobile;
    }

    a {
        display: table-cell;
        vertical-align: middle;
    }

    img {
        display: block;
        max-height: 100%;
        width: auto;
    }
}

/**
 * =============================
 * 2.0 - Navigation style
 * =============================
 */

.site-navigation {
    position: relative;
    text-align: right;
    position: absolute;
    top: 50%;
    transform: translate3d(0, -50%, 0);
    right: 0;

    .primary-menu {
        display: none; 
        text-align: left;

        @include breakpoint('above', 1100px) {
            display: block;
            text-align: right;
        }

        a {
            @include font-size(14);
            font-weight: 700;
            white-space: nowrap;
            line-height: 40px;
            padding: 0 15px;
            display: block;         
            color: #333;
            text-transform: uppercase;
            transition: 300ms ease;

            &:hover {
                background-color: #eee;
            }           
        }
        
        ul {
            margin: 0;
            padding: 0;
            box-shadow: 0 2px 4px rgba(#000, 0.15);
        }

        .sub-menu {
            text-align: left;
        }

        li {
            list-style: none;
            display: block;
            vertical-align: top;
        }

        .menu-item-has-children {
            position: relative;
            
            > a {
                padding-right: 30px;
            }

            svg {
                width: 16px;
                height: 40px;
                float: right;
                display: inline-block;
                vertical-align: middle;
                position: absolute;
                top: 0;
                right: 8px;
            }

            @include breakpoint('above', 750px) {

                .sub-menu {
                    opacity: 0;
                    visibility: hidden;
                    transition: 300ms;
                }
                
                &:hover {

                    > .sub-menu {
                        display: block !important;
                        visibility: visible;
                        opacity: 1;
                        transform: translate(0px);
                    }
                }
            }
        }

        > li {

            @include breakpoint('above', 750px) {
                display: inline-block;
            }

            > .sub-menu {
                display: none;

                @include breakpoint('above', 750px) {
                    display: block;
                    position: absolute;
                    top: 100%;
                    left: 0;
                    visibility: hidden;
                    transform: translateY(-5px);
                }

                a {
                    transition: 300ms ease;
                    background-color: #fff;
                    min-width: 200px;

                    &:hover {
                        background-color: #eee;
                    }                   
                }

                .sub-menu {
                    display: none;

                    @include breakpoint('above', 750px) {
                        display: block;
                        position: absolute;
                        top: 0;
                        left: 100%;
                        visibility: hidden;
                        transform: translateX(5px);
                    }
                }
            }
        }
    }
}